import React, { useEffect, useRef } from 'react';
import { Scene, WebGLRenderer, Color, TextureLoader, PerspectiveCamera, CylinderBufferGeometry, Mesh, MeshPhongMaterial, DirectionalLight, AmbientLight, PointLight, EquirectangularReflectionMapping, sRGBEncoding } from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';


const TestScene = props => {
  const { initialScale, modelUrl } = props;
  const anchorGltf = useRef();

  useEffect(() => {
    let isMounted = true;
    var camera, controls, scene, renderer;

    const onWindowResize = () => {
      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(window.innerWidth, window.innerHeight);
    }

    const initialize = () => {
      scene = new Scene();
      scene.background = new Color(0xcccccc);

      // const envTexture = new TextureLoader().load(`${process.env.PUBLIC_URL}/models/anchor/Textures/Passage_HDR_1K.jpeg`);

      // envTexture.mapping = EquirectangularReflectionMapping;
      // envTexture.encoding = sRGBEncoding;

      // scene.background = envTexture;

      renderer = new WebGLRenderer({ antialias: true });
      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.setSize(window.innerWidth, window.innerHeight);
      document.body.appendChild(renderer.domElement);

      camera = new PerspectiveCamera(60, window.innerWidth / window.innerHeight, 1, 1000);
      camera.position.set(400, 200, 0);

      // controls

      controls = new OrbitControls(camera, renderer.domElement);

      controls.enableDamping = true;
      controls.dampingFactor = 0.05;

      controls.screenSpacePanning = false;

      controls.minDistance = 100;
      controls.maxDistance = 500;

      controls.maxPolarAngle = Math.PI / 2;

      // lights

      var light1 = new PointLight(0xc9c9c9, 1);
      light1.position.set(50, 50, 200);
      scene.add(light1);

      var light2 = new PointLight(0xc9c9c9, 1);
      light2.position.set(-50, -50, 200);
      scene.add(light2);

      var light3 = new AmbientLight(0xffffff, 1);
      scene.add(light3);


      window.addEventListener('resize', onWindowResize, false);

      document.getElementById('ar-container').appendChild(renderer.domElement);

      new GLTFLoader()
        .load(modelUrl, gltf => {
          anchorGltf.current = gltf;

          console.log(gltf);
          anchorGltf.current.scene.scale.set(initialScale, initialScale, initialScale);
          scene.add(anchorGltf.current.scene);
          // anchorGltf.current.scene.position.z = -4;
        });
    }

    const update = () => {
      controls.update();
    };

    const render = () => {
      renderer.render(scene, camera);
    };
    
    
    const animate = () => {
      if (isMounted) {
        requestAnimationFrame(animate);
        update();
        render();
      }
    };

    initialize();
    animate();

    return () => {
      isMounted = false;
      document.getElementById('ar-container').removeChild(renderer.domElement);
    };
  }, [modelUrl, initialScale]);

  return (
    <div>
       Test
    </div>
  );
};

TestScene.defaultProps = {
  initialScale: 1,
  audioUrl: `${process.env.PUBLIC_URL}/audio/anchor.mp3`,
  modelUrl: `${process.env.PUBLIC_URL}/models/anchor/MiraiKoukai1.gltf`,
  patternUrl: `${process.env.PUBLIC_URL}/data/hiro.patt`,
  cameraParametersUrl: `${process.env.PUBLIC_URL}/data/camera_para.dat`
};

export default TestScene;
